/* Fifth Page Layout */
.fifth-page {
    background-color: #0d0d0d;
    color: white;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  
  /* Card Container Styling */
  .card-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    max-width: 1200px;
    width: 100%;
  }
  
  /* Individual Card Styling */
  .card {
    background-color: #1c1c1c;
    border-radius: 10px;
    overflow: hidden;
    text-align: center;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.1);
  }
  
  /* Card Content */
  .card-content {
    height: 200px; /* You can adjust this as needed */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Image Styling */
  .card-image {
    height: 50%; /* Image will occupy half the height of the card-content */
    width: auto; /* Maintain aspect ratio */
    border-radius: 10px;
    object-fit: cover; /* Ensures the image covers the space without distortion */
  }
  
  .card-text {
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
  }
  
  /* Description Styling */
  .card-description {
    margin-top: 5px;
    font-size: 14px;
    color: #b0b0b0; /* Light gray for the description text */
    padding: 0 10px; /* Optional padding for better text placement */
  }
  