/* Page Styling */
.fourth-page {
    background-color: #0d0d0d; /* Keeping the dark background */
    color: white;
    font-family: 'Arial', sans-serif;
    min-height: 100vh;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Categories Styling */
  .categories-container {
    display: flex;
    flex-wrap: wrap; /* Ensures the items wrap */
    gap: 20px;
    justify-content: center;
    align-content: center;
    width: 100%;
    height: auto;
    max-width: 1200px; /* Adjust the max width to better control box sizes */
  }
  
  /* Category Box Styling */
  .category-card {
    background: rgba(255, 255, 255, 0.1); /* Light, transparent background */
    border-radius: 10px;
    padding: 20px 30px;
    font-size: 18px;
    text-align: center;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2); /* Lighter shadow */
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(20% - 40px); /* 5 boxes per row, minus the gap */
    height: 80px; /* Adjust height to match the reference */
    min-width: 150px; /* Minimum width for responsiveness */
  }
  
  /* Transparent, light colors for each card */
  .category-card:nth-child(1) { background: rgba(173, 216, 230, 0.3); } /* Light blue */
  .category-card:nth-child(2) { background: rgba(218, 112, 214, 0.3); } /* Light purple */
  .category-card:nth-child(3) { background: rgba(255, 182, 193, 0.3); } /* Light pink */
  .category-card:nth-child(4) { background: rgba(135, 206, 235, 0.3); } /* Sky blue */
  .category-card:nth-child(5) { background: rgba(255, 165, 0, 0.3); } /* Light orange */
  .category-card:nth-child(6) { background: rgba(222, 184, 135, 0.3); } /* Beige */
  .category-card:nth-child(7) { background: rgba(147, 112, 219, 0.3); } /* Light violet */
  .category-card:nth-child(8) { background: rgba(255, 239, 213, 0.3); } /* Light peach */
  .category-card:nth-child(9) { background: rgba(221, 160, 221, 0.3); } /* Plum */
  .category-card:nth-child(10) { background: rgba(144, 238, 144, 0.3); } /* Light green */
  
  .category-card:hover {
    transform: scale(1.05); /* Subtle zoom on hover */
    background-color: rgba(255, 255, 255, 0.15); /* Slightly brighter on hover */
  }
  
  /* First row animation (moving right) */
  .first-row {
    animation: moveRight 3s ease-in-out infinite alternate;
  }
  
  /* Second row animation (moving left) */
  .second-row {
    animation: moveLeft 3s ease-in-out infinite alternate;
  }
  
  /* Keyframes for moving right */
  @keyframes moveRight {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(20px); /* Move right by 20px */
    }
  }
  
  /* Keyframes for moving left */
  @keyframes moveLeft {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-20px); /* Move left by 20px */
    }
  }
  